section {
    font-family: "PT Serif", serif;
    position: relative;
    border-radius: 0.5rem;
    background-color: #D0DBF5;
    margin: 2rem 3rem 0 6rem;
  }
  section::before {
    content: " ";
    margin-top: 1rem;
    border-width: 1.2rem;
    border-style: solid;
    border-color: transparent #D0DBF5 transparent transparent;
    position: absolute;
    right: 100%;
    top: 0;
  }
  section .fa {
    position: absolute;
    right: calc(100% + 1.5rem);
    top: 0.7rem;
    width: 3rem;
    height: 3rem;
    font-size: 1.6em;
    line-height: 3rem;
    text-align: center;
    border-radius: 50%;
    background-color: #082568;
    color: #FFE463;
  }
  .fa-death::before {
    content: "\271D";
  }
  h3 {
    padding: 1rem 2rem 0 1rem;
    font-size: 1.6em;
    color: #20096A;
  }
  .year {
    float: right;
    color: #C5A300;
  }
  .year::after,
  .year::before {
    content: "-";
  }
  section p {
    padding: 1rem;
    color: rgba(0, 0, 0, 0.75);
    font-size: 1.6em;
  }
  
  @media only screen and (min-width: 950px) {
    section {
      margin: 0;
      width: 45%;
    }
    section:nth-child(odd) {
      -ms-flex-item-align: end;
          align-self: flex-end;
      margin-top: -2rem;
    }
    section:nth-child(even) {
      margin-top: -2rem;
    }
    section:nth-child(even)::before {
      left: 100%;
      right: initial;
      border-color: transparent transparent transparent #D0DBF5;
    }
    section .fa {
      top: 0.25rem;
      width: 4rem;
      height: 4rem;
      line-height: 4rem;
    }
    section:nth-child(even) .fa {
      left: calc(111.111111111111% - 2rem);
      right: initial;
    }
    section:nth-child(odd) .fa {
      right: calc(111.111111111111% - 2rem);
    }
}